<template>
  <div>
    <el-dialog
      :title="modifyTitle"
      :visible.sync="v"
      :before-close="handleClose"
      width="520px"
      :close-on-click-modal="false"
      append-to-body
    >
      <el-form
        ref="paramFormRef"
        :model="paramForm"
        :rules="paramRules"
        label-width="80px"
        size="small"
        class="demo-ruleForm"
      >
        <el-form-item label="全能卡号" prop="fullMsisdn">
          <el-input v-model="paramForm.fullMsisdn" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="主套餐">
          <el-input v-model="paramForm.mainOfferName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="加餐包" prop="offerId">
          <el-select v-model="paramForm.offerId" placeholder="请选择">
            <el-option
              v-for="item in offerOptions"
              :key="item.offerId"
              :label="item.offerName"
              :value="item.offerId"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" :loading="btnLoading" @click="clickOk"
          >确 定</el-button
        >
        <el-button size="small" @click="cancel">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'ChangePackageDialog',
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    modifyTitle: {
      type: String,
      default: '套餐订购',
    },
    detailInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      paramForm: {
        fullMsisdn: '',
        offerId: null,
        mainOfferName: '',
      },
      statusCd: '1000',
      paramRules: {
        offerId: [{ required: true, message: '请选择目标套餐', trigger: 'blur' }],
      },
      offerOptions: [],
      btnLoading: false,
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },

  mounted() {
    const { fullMsisdn, mainOfferName } = this.detailInfo
    this.paramForm.fullMsisdn = fullMsisdn
    this.paramForm.mainOfferName = mainOfferName
    this.queryGeneralFullOfferListByMsisdn()
  },
  methods: {
    async queryGeneralFullOfferListByMsisdn() {
      try {
        const params = {
          fullMsisdn: this.detailInfo.fullMsisdn,
          custId: this.detailInfo.custId,
          offerType: '300',
          offerBillType: '301',
        }
        this.$api.fullCardMag.queryGeneralFullOfferListByMsisdn(params).then((res) => {
          this.offerOptions = res || []
        })
      } catch (e) {}
    },
    clickOk() {
      this.$refs.paramFormRef.validate((valid) => {
        if (!valid) return
        this.subscribeFullSingleAddPackage()
      })
    },
    async subscribeFullSingleAddPackage() {
      this.btnLoading = true
      try {
        const { fullMsisdn, offerId } = this.paramForm
        const params = {
          fullMsisdn,
          offerId,
          custId: this.detailInfo.custId,
        }
        this.$api.fullCardMag
          .subscribeFullSingleAddPackage(params)
          .then((res) => {
            this.$message.success(`订购成功！`)
            this.v = false
            this.$emit('refresh')
          })
          .finally(() => {
            this.btnLoading = false
          })
      } catch (e) {
        this.btnLoading = false
      }
    },
    cancel() {
      this.v = false
    },
    handleClose() {
      this.cancel()
    },
  },
}
</script>
<style lang="scss" scoped></style>
