<template>
  <div class="summary">
    <el-table
      ref="table"
      v-loading="loading"
      :element-loading-text="$t('cardMag.loading')"
      :data="tableData"
      :border="true"
    >
      <el-table-column
        prop="serviceOfferDesc"
        :label="$t('cardMag.serviceOfferDesc')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="oldValueDec"
        :label="$t('cardMag.oldValueDec')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="newValueDec"
        :label="$t('cardMag.newValueDec')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="changeDate"
        :label="$t('cardMag.changeDate')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="changeStaffName"
        :label="$t('cardMag.changeStaffName')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="statusCdName"
        :label="$t('cardMag.statusCdName')"
        show-overflow-tooltip
      ></el-table-column>
    </el-table>
    <el-pagination
      v-show="tableData.length > 0"
      background
      :current-page="page.pageIndex"
      align="right"
      :page-sizes="[10, 20, 50]"
      :page-size="page.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="page.total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    >
    </el-pagination>
  </div>
</template>

<script>
import fetch from '@/request/fetch'
export default {
  props: {
    fullMsisdn: {
      type: String,
      required: true,
      default: () => null,
    },
  },
  data() {
    return {
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      loading: false,
    }
  },
  mounted() {
    this.qryFullSimChangeHis()
  },
  methods: {
    async qryFullSimChangeHis() {
      try {
        this.loading = true
        const param = { fullMsisdn: this.fullMsisdn, ...this.page }
        this.$api.fullCardMag
          .qryFullSimChangeHis(param)
          .then((res) => {
            this.tableData = res.list || []
            this.page.total = res.total || 0
          })
          .finally(() => {
            this.loading = false
          })
      } catch (e) {
        this.loading = false
      }
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.qryFullSimChangeHis()
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.qryFullSimChangeHis()
    },
  },
}
</script>
<style lang="scss" scoped>
.summary {
  // display: flex;
  margin-top: 24px;
}
</style>
