<template>
  <div>
    <div class="summary">
      <div v-for="item in summaryList" :key="item.title" class="summary-item">
        <div class="summary-item-title">{{ item.title }}</div>
        <div class="summary-item-used">
          {{ item.used }} <span class="summary-item-unit">MB</span>
        </div>
      </div>
    </div>
    <el-descriptions
      v-if="testFullOfferInst && testFullOfferInst.offerName"
      :title="testFullOfferInst.offerName"
      direction="vertical"
      :column="4"
      border
    >
      <el-descriptions-item :label="$t('cardMag.offerName')">{{
        testFullOfferInst.offerName
      }}</el-descriptions-item>
      <el-descriptions-item :label="$t('cardMag.offerUsageDesc')">{{
        testFullOfferInst.offerUsageDesc ? testFullOfferInst.offerUsageDesc + 'MB' : ''
      }}</el-descriptions-item>
      <el-descriptions-item :label="$t('cardMag.billUsageDesc')">{{
        testFullOfferInst.billUsageDesc ? testFullOfferInst.billUsageDesc + 'MB' : ''
      }}</el-descriptions-item>
      <el-descriptions-item :label="$t('cardMag.effDate')">{{
        testFullOfferInst.effDate
      }}</el-descriptions-item>
      <el-descriptions-item :label="$t('cardMag.expDate')">{{
        testFullOfferInst.expDate
      }}</el-descriptions-item>
    </el-descriptions>
    <el-descriptions
      v-if="mainFullOfferInst && mainFullOfferInst.offerName"
      :title="mainFullOfferInst.offerName"
      direction="vertical"
      :column="6"
      border
    >
      <el-descriptions-item :label="$t('cardMag.offerName')">{{
        mainFullOfferInst.offerName
      }}</el-descriptions-item>
      <el-descriptions-item :label="$t('cardMag.offerUsageDesc')">{{
        mainFullOfferInst.offerUsageDesc ? mainFullOfferInst.offerUsageDesc + 'MB' : ''
      }}</el-descriptions-item>
      <el-descriptions-item :label="$t('cardMag.billUsageDesc')">{{
        mainFullOfferInst.billUsageDesc ? mainFullOfferInst.billUsageDesc + 'MB' : ''
      }}</el-descriptions-item>
      <el-descriptions-item :label="$t('cardMag.flowPoolInstId')">{{
        mainFullOfferInst.flowPoolInstId
      }}</el-descriptions-item>
      <el-descriptions-item :label="$t('cardMag.effDate')">{{
        mainFullOfferInst.effDate
      }}</el-descriptions-item>
      <el-descriptions-item :label="$t('cardMag.expDate')">{{
        mainFullOfferInst.expDate
      }}</el-descriptions-item>
      <!--      <el-descriptions-item label="服务状态" v-compPriv="112">-->
      <!--        <el-switch v-model="isOpen" @change="handleChangeStatus"></el-switch>-->
      <!--      </el-descriptions-item>-->
    </el-descriptions>
    <SupplyOfferTable :qry-offer-inst-by-prod-inst-res="queryFullOfferInstsRes" />
  </div>
</template>

<script>
import SupplyOfferTable from './components/SupplyOfferTable'
export default {
  components: {
    SupplyOfferTable,
  },
  props: {
    fullMsisdn: {
      type: String,
      required: true,
      default: () => null,
    },
    blockStatus: {
      type: String,
      default: () => null,
    },
  },
  data() {
    return {
      value1: true,
      loading: false,
      summaryList: [
        {
          title: this.$t('cardMag.planTotalUsage'),
          used: '',
          key: 'planTotalUsage',
        },
        {
          title: this.$t('cardMag.totalBillUsage'),
          used: '',
          key: 'totalBillUsage',
        },
        {
          title: this.$t('cardMag.totalOverUsage'),
          used: '',
          key: 'totalOverUsage',
        },
      ],
      queryFullOfferInstsRes: {},
      mainFullOfferInst: {},
      testFullOfferInst: {},
      planTotalUsage: '',
      totalBillUsage: '',
      totalOverUsage: '',
      isOpen: false, // 1000 代表开 其他值代表关
    }
  },
  watch: {
    blockStatus(newnew, oldold) {
      this.isOpen = newnew === '1000'
    },
  },
  mounted() {
    this.queryFullOfferInsts()
  },
  methods: {
    async queryFullOfferInsts() {
      try {
        const param = { fullMsisdn: this.fullMsisdn }
        this.$api.fullCardMag.queryFullOfferInsts(param).then((res) => {
          this.queryFullOfferInstsRes = res
          this.mainFullOfferInst = res.mainFullOfferInst ?? {}
          this.testFullOfferInst = res.testFullOfferInst ?? {}
          this.handle(res)
        })
      } catch (e) {}
    },
    handle(val) {
      this.summaryList.forEach((item) => {
        item.used = val[item.key]
      })
    },
    async handleChangeStatus(val) {
      try {
        const newBlockStatus = val ? '1000' : '1100'
        const param = { fullMsisdn: this.fullMsisdn, newBlockStatus }
        this.$api.fullCardMag.changeFullDataServiceStatus(param).then((res) => {
          this.$message.success(`成功！`)
          this.qrySimDetail()
        })
      } catch (e) {}
    },
    async qrySimDetail() {
      const param = { fullMsisdn: this.fullMsisdn }
      this.$api.fullCardMag.qryFullSimDetail(param).then((res) => {
        this.isOpen = res.blockStatus === '1000'
      })
    },
  },
}
</script>

