<template>
  <div>
    <div class="summary">
      <el-descriptions direction="vertical" :column="5" border>
        <el-descriptions-item :label="$t('cardMag.ispName')">{{
          info.ispName
        }}</el-descriptions-item>
        <el-descriptions-item :label="$t('cardMag.apnName')">{{
          info.apnName
        }}</el-descriptions-item>
        <el-descriptions-item :label="$t('cardMag.onlineStatus')">{{
          info.onlineStatus
        }}</el-descriptions-item>
        <el-descriptions-item :label="$t('cardMag.startTime')">{{
          info.startTime
        }}</el-descriptions-item>
        <el-descriptions-item :label="$t('cardMag.ipv4Address')">{{
          info.ipv4Address
        }}</el-descriptions-item>
        <el-descriptions-item :label="$t('cardMag.ipv6Address')">{{
          info.ipv6Address
        }}</el-descriptions-item>
      </el-descriptions>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    fullMsisdn: {
      type: String,
      required: true,
      default: () => null,
    },
  },
  data() {
    return {
      info: {},
    }
  },
  mounted() {
    this.qryFullSimCardOnlineInfo()
  },
  methods: {
    async qryFullSimCardOnlineInfo() {
      const param = { fullMsisdn: this.fullMsisdn }
      this.$api.fullCardMag.qryFullSimCardOnlineInfo(param).then((res) => {
        this.info = res || {}
      })
    },
  },
}
</script>

