<template>
  <div>
    <el-dialog
      :title="modifyTitle"
      :visible.sync="v"
      :before-close="handleClose"
      width="520px"
      :close-on-click-modal="false"
      append-to-body
    >
      <el-form
        ref="paramFormRef"
        :model="paramForm"
        :rules="paramRules"
        label-width="110px"
        size="small"
        class="demo-ruleForm"
      >
        <el-form-item label="全能卡号" prop="fullMsisdn">
          <el-input v-model="paramForm.fullMsisdn" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="主套餐">
          <el-input v-model="paramForm.mainOfferName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="号卡状态" prop="simStatusDesc">
          <el-input v-model="paramForm.simStatusDesc" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="目标号卡状态" prop="newSimStatus">
          <el-select v-model="paramForm.newSimStatus" placeholder="请选择">
            <el-option
              v-for="item in simStatusList"
              :key="item.simStatus"
              :label="item.simStatusDesc"
              :value="item.simStatus"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="渠道商" prop="channelId">
          <el-select v-model="paramForm.channelId" placeholder="请选择">
            <el-option :value="0" :label="'-'"></el-option>
            <el-option
              v-for="item in channelIdOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" :loading="btnLoading" @click="clickOk"
          >确 定</el-button
        >
        <el-button size="small" @click="cancel">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import fetch from '@/request/fetch'
export default {
  name: 'ChangAttrDialog',
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    modifyTitle: {
      type: String,
      default: '属性变更',
    },
    detailInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      paramForm: {
        custId: null,
        fullMsisdn: '',
        mainOfferName: '',
        simStatusDesc: '',
        newSimStatus: '',
        channelId: null,
      },
      simStatusList: [],
      paramRules: {
        channelId: [{ required: true, message: '请选择渠道商', trigger: 'blur' }],
        newSimStatus: [
          { required: true, message: '请选择目标号卡状态', trigger: 'blur' },
        ],
      },
      channelIdOptions: [],
      simStatus: null,
      btnLoading: false,
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  mounted() {
    const { fullMsisdn, channelId, custId, mainOfferName, simStatusDesc, simStatus } =
      this.detailInfo
    this.paramForm.fullMsisdn = fullMsisdn
    this.paramForm.channelId = channelId
    this.paramForm.custId = custId
    this.paramForm.mainOfferName = mainOfferName
    this.paramForm.simStatusDesc = simStatusDesc
    this.simStatus = simStatus
    this.handler(this.paramForm.custId)
    this.getChangeSimStatusList()
  },
  methods: {
    async handler(val) {
      if (!val) return
      const res = await fetch(
        '/channel/queryChannelList',
        { custId: val },
        { method: 'post' }
      )
      this.channelIdOptions = res.map((item) => {
        return {
          value: item.channelId,
          label: item.channelName,
        }
      })
    },
    async getChangeSimStatusList() {
      try {
        const params = { simStatus: this.simStatus }
        this.$api.fullCardMag.getFullChangeSimStatusList(params).then((res) => {
          this.simStatusList = res || []
        })
        this.paramForm.newSimStatus = this.detailInfo.simStatus
      } catch (e) {}
    },
    clickOk() {
      this.$refs.paramFormRef.validate((valid) => {
        if (!valid) return
        this.changeFullSimAttr()
      })
    },
    async changeFullSimAttr() {
      this.btnLoading = true
      try {
        const { fullMsisdn, channelId, newSimStatus } = this.paramForm
        const params = {
          fullMsisdn,
          channelId,
          newSimStatus,
        }
        this.$api.fullCardMag
          .changeFullSimAttr(params)
          .then((res) => {
            this.$message.success(`变更成功！`)
            this.v = false
            this.$parent.qrySimDetail()
          })
          .finally(() => {
            this.btnLoading = false
          })
      } catch (e) {
        this.btnLoading = false
      }
    },
    cancel() {
      this.v = false
    },
    handleClose() {
      this.cancel()
    },
  },
}
</script>
<style lang="scss" scoped></style>
