<template>
  <div v-if="supplyFullOfferInstList">
    <div v-for="(item, index) in supplyFullOfferInstList" :key="index">
      <div class="section-title">{{ item[0].offerName }}</div>
      <el-table
        ref="table"
        class="container-table"
        element-loading-text="加载中"
        :data="item"
        :border="true"
        height="100%"
      >
        <el-table-column
          prop="offerUsageDesc"
          :label="$t('cardMag.offerUsageDesc')"
          show-overflow-tooltip
          resizable
          min-width="110"
        >
          <template slot-scope="{ row }">{{
            row.offerUsageDesc ? row.offerUsageDesc + 'MB' : ''
          }}</template>
        </el-table-column>
        <el-table-column
          prop="billUsageDesc"
          :label="$t('cardMag.billUsageDesc')"
          show-overflow-tooltip
          resizable
          min-width="110"
        >
          <template slot-scope="{ row }">{{
            row.billUsageDesc ? row.billUsageDesc + 'MB' : ''
          }}</template>
        </el-table-column>
        <el-table-column
          prop="createTime"
          :label="$t('cardMag.orderTime')"
          show-overflow-tooltip
          resizable
          min-width="110"
        >
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    qryOfferInstByProdInstRes: {
      type: Object,
      required: true,
      default: () => {},
    },
  },
  computed: {
    supplyFullOfferInstList() {
      const supplyFullOfferInstList =
        this.qryOfferInstByProdInstRes.supplyFullOfferInstList
      if (!supplyFullOfferInstList) {
        return []
      }
      const list = this.groupByOfferId(supplyFullOfferInstList)
      return list
    },
  },
  methods: {
    groupByOfferId(arr) {
      // 将 offerId 相同的合并到一个数组里，最后返回二维数组
      return arr.reduce((groupList, currentValue) => {
        if (
          !groupList.some((list) =>
            list.some((item) => item.offerId === currentValue.offerId)
          )
        ) {
          groupList.push([currentValue])
        } else {
          groupList
            .find((list) => list.some((item) => item.offerId === currentValue.offerId))
            .push(currentValue)
        }
        return groupList
      }, [])
    },
  },
}
</script>

<style lang="scss" scoped>
.section-title {
  font-weight: 500;
  font-size: 14px;
  color: #333333;
  padding: 24px 0;
}
</style>
